import { NextRequest, NextResponse } from 'next/server'
import { createAIClient, parseAIResponse } from '@/lib/ai-client'

export async function POST(request: NextRequest) {
  try {
    const body = await request.json()
    const { team1, team2, team1Stats, team2Stats, gameDate, liveScore } = body

    if (!team1 || !team2 || !team1Stats || !team2Stats) {
      return NextResponse.json(
        { error: 'Missing required fields: team1, team2, team1Stats, team2Stats' },
        { status: 400 }
      )
    }

    // Build the AI prompt
    const prompt = buildGameOutcomePrompt(team1, team2, team1Stats, team2Stats, gameDate, liveScore)

    // Use unified AI client with automatic fallback
    try {
      const aiClient = createAIClient()
      
      const aiResponse = await aiClient.complete({
        messages: [
          {
            role: 'system',
            content: 'You are an expert NBA game analyst. You predict game outcomes based on team and player statistics. Always respond in JSON format with winner, confidence, reasoning, scorePrediction, keyFactors, and winProbability fields.'
          },
          {
            role: 'user',
            content: prompt
          }
        ],
        temperature: 0.3,
        max_tokens: 700,
      })

      // Parse AI response with fallback
      const prediction = parseAIResponse(aiResponse.content, {
        winner: team1,
        confidence: 'Medium',
        reasoning: aiResponse.content,
        scorePrediction: 'N/A',
        keyFactors: [],
        winProbability: 50
      })
      
      // Log which model was used
      if (aiResponse.fallback_triggered) {
        console.log(`[Game Outcome] Fallback model used: ${aiResponse.model_used}`)
      }

      return NextResponse.json({
        success: true,
        team1,
        team2,
        gameDate,
        ...prediction,
        modelUsed: aiResponse.model_used,
        fallbackTriggered: aiResponse.fallback_triggered
      })
    } catch (aiError: any) {
      console.error('AI Client Error:', aiError)
      return NextResponse.json(
        { error: aiError.message || 'AI service error' },
        { status: aiError.statusCode || 500 }
      )
    }

  } catch (error) {
    console.error('Error in AI game prediction route:', error)
    return NextResponse.json(
      { error: error instanceof Error ? error.message : 'Internal server error' },
      { status: 500 }
    )
  }
}

function buildGameOutcomePrompt(
  team1: string,
  team2: string,
  team1Stats: any,
  team2Stats: any,
  gameDate?: string,
  liveScore?: { status: string, team1Score?: number, team2Score?: number, venue?: string }
): string {
  let gameContext = ''
  
  if (liveScore?.status === 'live') {
    gameContext = `\n\nðŸ”´ LIVE GAME IN PROGRESS:\nCurrent Score: ${team1} ${liveScore.team1Score || 0} - ${liveScore.team2Score || 0} ${team2}\n\nBased on the current score and team statistics, provide an updated prediction for the final outcome.`
  } else if (liveScore?.status === 'final') {
    gameContext = `\n\nâœ… GAME COMPLETED:\nFinal Score: ${team1} ${liveScore.team1Score || 0} - ${liveScore.team2Score || 0} ${team2}\n\nProvide analysis of the outcome based on the final score and pre-game statistics.`
  }
  
  const prompt = `
Analyze the NBA game${gameDate ? ` on ${gameDate}` : ''} between ${team1} and ${team2}${liveScore?.status ? ` (Status: ${liveScore.status.toUpperCase()})` : ' and predict the outcome'}.${gameContext}

${team1.toUpperCase()} STATISTICS:
- Record: ${team1Stats.wins || 0} - ${team1Stats.losses || 0}
- Win Percentage: ${((team1Stats.wins || 0) / ((team1Stats.wins || 0) + (team1Stats.losses || 0)) * 100).toFixed(1)}%
- Points Per Game: ${team1Stats.pointsPerGame || 0}
- Points Allowed: ${team1Stats.pointsAllowed || 0}
- Rebounds Per Game: ${team1Stats.reboundsPerGame || 0}
- Assists Per Game: ${team1Stats.assistsPerGame || 0}
- Field Goal %: ${team1Stats.fieldGoalPct || 0}%
- Three Point %: ${team1Stats.threePointPct || 0}%
- Recent Form (Last 5): ${team1Stats.last5 || 'N/A'}
${team1Stats.topPlayers ? `\nTop Players:\n${team1Stats.topPlayers.map((p: any) => `- ${p.name}: ${p.ppg} PPG, ${p.rpg} RPG, ${p.apg} APG`).join('\n')}` : ''}

${team2.toUpperCase()} STATISTICS:
- Record: ${team2Stats.wins || 0} - ${team2Stats.losses || 0}
- Win Percentage: ${((team2Stats.wins || 0) / ((team2Stats.wins || 0) + (team2Stats.losses || 0)) * 100).toFixed(1)}%
- Points Per Game: ${team2Stats.pointsPerGame || 0}
- Points Allowed: ${team2Stats.pointsAllowed || 0}
- Rebounds Per Game: ${team2Stats.reboundsPerGame || 0}
- Assists Per Game: ${team2Stats.assistsPerGame || 0}
- Field Goal %: ${team2Stats.fieldGoalPct || 0}%
- Three Point %: ${team2Stats.threePointPct || 0}%
- Recent Form (Last 5): ${team2Stats.last5 || 'N/A'}
${team2Stats.topPlayers ? `\nTop Players:\n${team2Stats.topPlayers.map((p: any) => `- ${p.name}: ${p.ppg} PPG, ${p.rpg} RPG, ${p.apg} APG`).join('\n')}` : ''}

ANALYSIS FACTORS:
1. Overall team record and win percentage
2. Offensive efficiency (PPG) vs defensive efficiency (points allowed)
3. Recent form and momentum
4. Key player performance and matchups
5. Shooting efficiency (FG% and 3P%)
6. Rebounding and assist differentials

Based on this comprehensive data, provide a prediction in the following JSON format:
{
  "winner": "${team1}" or "${team2}",
  "confidence": "High" or "Medium" or "Low",
  "reasoning": "3-5 sentence explanation based on the stats and matchups provided",
  "scorePrediction": "predicted score (e.g., '112-108')",
  "keyFactors": ["factor 1", "factor 2", "factor 3"],
  "winProbability": number (percentage chance of winner winning, 0-100)
}

Be specific and data-driven. Focus on statistical advantages and matchup analysis.
`

  return prompt
}
